body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eff3f7;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
  padding: 50px;
  -webkit-justify-content: space-around;
          justify-content: space-around; /*요소사이 공간*/
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap; /* 플렉스 요소가 여유 공간이 없으면 다음 줄로 넘어감 */
  font-size: 15px;
}

.Movie {
  background-color: white;
  width: 40%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between; /* 포스터랑 내용사이 */
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-bottom: 55px;
  padding: 0 20px;
  box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3),
    0 5px 12px rgba(133, 133, 133, 0.22);
  text-overflow: ellipsis;
}

/*포스터부분*/
.Movie__Columns {
  width: 30%;
  box-sizing: border-box;
  /* text-overflow: ellipsis; */
}

/*내용*/
.Movie__Columns:last-child {
  width: 60%;
  padding: 25px 0;
}

.Movie h1 {
  font-size: 24px;
  font-weight: 400;
}

.Movie .Movie__Genres {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-bottom: 20px;
}

.Movie__Genres .Movie__Genre {
  margin-right: 10px;
  color: #abacb2;
  font-size: 16px;
}

.Movie .Movie__Rating {
  color: #fbc531;
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 20px;
}

.Movie .Movie__Synopsis {
  text-overflow: ellipsis;
  color: #abacb2;
  overflow: hidden;
}

.Movie .Movie__Poster {
  max-width: 100%;
  position: relative;
  top: -25px;
  box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3),
    10px 15px 12px rgba(80, 80, 80, 0.22);
}

@media screen and (min-width: 320px) and (max-width: 667px) {
  .Movie {
    width: 100%;
  }
}

/*세로모드*/
@media screen and (min-width: 320px) and (max-width: 667px) and (orientation: portrait) {
  .Movie {
    width: 100%;
    -webkit-flex-direction: column;
            flex-direction: column;
  }
  .Movie__Poster {
    top: 0;
    left: 0;
    width: 100%;
  }
  .Movie__Columns {
    width: 100% !important;
  }
}

